.navBar {
  background-color: #171717; /* Preto */
  color: #ffffff; /* Branco */
  padding: 10px;
  display: flex;
  justify-content: space-around;
}

.navBar ul {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.navBar a {
  color: #ffffff;
  text-decoration: none;
}

.navBar a:hover {
  text-decoration: underline;
}
